import React,{Component} from 'react';
import {PageHeader,Button,Input,Spin,Table,Modal,Radio,message,Steps,Tag,Divider, Select, Avatar, Icon} from 'antd';
import ComponentForm from './componentForm';
import ParamsForm from './paramsForm';
import InputOutputForm from './inputOutputForm';
import {getCustomComponent,deleteCustomComponent,deleteCustomComponentPermanently,restoreCustomComponent,
    searchCustomComponentByName,searchCustomComponentByTag,createCustomComponent,getCustomComponentInTrash} from '../../api/component';
import './index.less';

class CustomComponent extends Component{

    state={
        loading:false,
        dataSource:[],
        type:'1',
        searchWord:'',
        visible:false,
        radioValue:'1',
        listLoading:false,
        newVisible:false,
        newType:'',
        current:0,
        values:{},
        paramsValues:[],
        dataInTrash:[],
        selectedRowKeys:[],
        searchType:'name',
        inputOutput:[],
        total:0,
        page:1,
        trashTotal:0,
        trashPage:1,
        fileList:[]
    }

    getComponent=()=>{
        const {type,page}=this.state;
        this.setState({
            loading:true
        })
        const params={
            type,
            pageSize:5,
            pageNum:page,
            // userId:2
        }
        getCustomComponent(params).then((res)=>{
            if(res.data.meta.code=='001'){
                const data=res.data.data;
                const dataSource=data.list.map((item,index)=>({
                    ...item,
                    index:index+1+(page-1)*10
                }))
                this.setState({
                    loading:false,
                    dataSource,
                    total:data.size
                })
            }
        }).catch(err=>{
            message.error('获取数据出错!')
            console.log(err)
            this.setState({
                loading:false,
            })
        })
    }
    getComponentInTrash=()=>{
        const {radioValue,trashPage}=this.state;
        this.setState({
            listLoading:true
        })
        const params={
            type:radioValue,
            pageSize:6,
            pageNum:trashPage,
            // userId:2
        }
        getCustomComponentInTrash(params).then((res)=>{
            if(res.data.meta.code==='001'){
                const data=res.data.data;
                const dataInTrash=data.list.map((item,index)=>({
                    ...item,
                    index:index+1+(trashPage-1)*10,
                    key:item.id
                }))
                this.setState({
                    listLoading:false,
                    dataInTrash,
                    trashTotal:data.size
                })
            }
        }).catch(err=>{
            message.error('获取数据出错!')
            this.setState({
                listLoading:false,
            })
        })
    }

    clickButton=(t)=>{
        this.setState({
            type:t,
            page:1
        }, this.getComponent)
    }
    changeSearchWord=(e)=>{
        this.setState({
            searchWord:e.target.value
        })
    }
    changePage=(page)=>{
        this.setState({
            page
        })
        if(this.state.searchWord){
            this.searchComponent();
        }else{
            this.getComponent();
        }
        
    }
    openModal=()=>{
        this.setState({
            visible:true,
            trashPage:1
        },this.getComponentInTrash)
        
    }
    closeModal=()=>{
        this.setState({
            visible:false
        })
        this.getComponent();
    }
    changeRadioValue=(e)=>{
        this.setState({
            radioValue:e.target.value,
            trashPage:1
        },this.getComponentInTrash)
    }
    changeDeleteItem=(selectedRowKeys,selectedRows)=>{
        this.setState({
            selectedRowKeys
        })
    }
    openNew=()=>{
        this.setState({
            newVisible:true
        })
    }
    createComponent=()=>{
        const componentCreateInfo={};
        this.inputOutputForm.props.form.validateFields((err,values)=>{
            if(!err){
                this.setState({
                    inputOutput:this.handleParamsValues(values)
                },()=>{
                    const {values,paramsValues,inputOutput}=this.state;
                    const v=JSON.parse(JSON.stringify(values));
                    delete v.sourceId;
                    delete v.package;
                    const input=inputOutput.map(i=>({inputName:i.inputName,inputType:i.inputType})) 
                    const output=inputOutput.map(i=>({outputName:i.outputName,outputType:i.outputType}))
                    componentCreateInfo.componentType=values.type;
                    componentCreateInfo.sourceId=parseInt(values.sourceId);
                    componentCreateInfo.inputStubInfoList=input;
                    componentCreateInfo.outputStubInfoList=output;
                    componentCreateInfo.componentInfo=v;
                    componentCreateInfo.componentParamList=paramsValues;
                    console.log(v);
                    const params={
                        componentCreateInfo:JSON.stringify(componentCreateInfo),
                        componentFile:values.package?values.package.file:new File(["foo"], "foo.txt", {
                            type: "text/plain",
                          }),

                    }
                    console.log(params)
                    createCustomComponent(params).then(res=>{
                        if(res.data.meta.code==='001'){
                            message.success('创建组件成功!');
                            this.setState({
                                newVisible:false,
                                current:0,
                                values:{},
                                paramsValues:{},
                                inputOutput:{}
                            })
                            this.componentForm.props.form.resetFields();
                            this.componentForm.clearFileList();
                            this.getComponent();
                        }else{
                            message.error('创建组件失败!');
                        }
                    }).catch(err=>{
                        message.error('创建组件失败!')
                    })
                    
                })
            }else{

            }
        })
    }
    closeNew=()=>{
        this.setState({
            newVisible:false,
            current:0,
            values:{},
            paramsValues:{},
            inputOutput:{}
        })
        console.log('em')
        this.componentForm.props.form.resetFields();
        this.componentForm.clearFileList();
    }
    nextStep=()=>{
        const {current}=this.state;
        if(current===0){
            this.componentForm.props.form.validateFields((err,values)=>{
                if(!err){
                    this.setState({
                        current:current+1,
                        values
                    })
                }else{
                    message.warning('请填写完整信息!')
                }
            });
        }else{
            this.paramsForm.props.form.validateFields((err,values)=>{
                if(!err){
                    this.setState({
                        current:current+1,
                        paramsValues:this.handleParamsValues(values)
                    })
                }else{
    
                }
            })
        }
    }
    preStep=()=>{
        const {current}=this.state;
        if(current===2){
            this.inputOutputForm.props.form.validateFields((err,values)=>{
                if(!err){
                    this.setState({
                        current:current-1,
                        inputOutput:this.handleParamsValues(values)
                    })
                }else{
    
                }
            })
        }else{
            this.paramsForm.props.form.validateFields((err,values)=>{
                if(!err){
                    this.setState({
                        current:current-1,
                        paramsValues:this.handleParamsValues(values)
                    })
                }else{
    
                }
            })
        }
    }
    handleParamsValues=(values)=>{
        if(values.name||values.inputName){
            var l;
            if(values.name){
                l=values.name.length;
            }else{
                l=values.inputName.length;
            }
            const p=[];
            for(let j=0;j<l;j++){
                let obj={};
                for(let i in values){
                    if(i!=='keys'&&values[i][j]){
                        obj[i]=values[i][j];
                    }
                }
                if(JSON.stringify(obj)!=='{}')  p.push(obj);
            }
            return p;
        }else{
            return []
        }
    }
    deleteComponent=(id)=>{
        const params={
            componentId:id
        }
        deleteCustomComponent(params).then((res)=>{
            if(res.data.meta.code==='001'){
                message.success('删除成功!');
            }else{
                message.error('删除失败!');
            }
            this.getComponent();
        }).catch(err=>{

        })
    }
    deleteComponentPermanently=(id)=>{
        const params={};
        if(typeof id=='number'){
            params.componentIds=id+'';
        }else{
            params.componentIds=this.state.selectedRowKeys.join(',')
        }
        deleteCustomComponentPermanently(params).then(res=>{
            if(res.data.meta.code==='001'){
                message.success('删除成功!');
            }else{
                message.error('删除失败!');
            }
            this.getComponentInTrash();
        }).catch(err=>{
            message.error('删除失败!');
        })
    }
    restoreComponent=(id)=>{
        console.log(this.state.selectedRowKeys);
        const params={};
        if(typeof id=='number'){
            params.componentIds=id+'';
        }else{
            params.componentIds=this.state.selectedRowKeys.join(',')
        }
        restoreCustomComponent(params).then(res=>{
            if(res.data.meta.code==='001'){
                message.success('还原成功!');
            }else{
                message.error('还原失败!');
            }
            this.getComponentInTrash();
        }).catch(err=>{
            message.error('还原失败!');
        })
    }
    changeSearchType=(v)=>{
        this.setState({
            searchType:v
        })
    }
    handleSearch=()=>{
        var {searchWord}=this.state;
        searchWord.replace(/\s/g,(a)=>'');
        if(searchWord==''){
            message.warning('搜索关键字不能为空格字符串！')
        }else{
            this.setState({
                page:1
            },this.searchComponent)
        }
    }
    searchComponent=()=>{
        const {searchType,searchWord,page,type}=this.state;
        this.setState({
            loading:true
        })
        const params={
            pageSize:5,
            pageNum:page,
            type,
            // userId:2
        }
        if(searchType==='name'){
            params.keyword=searchWord;
            searchCustomComponentByName(params).then(res=>{
                if(res.data.meta.code==='001'){
                    console.log(res.data)
                    const data=res.data.data;
                    const dataSource=data.ComponentList.list.map((item,index)=>({
                        ...item,
                        index:index+1+(page-1)*10
                    }))
                    this.setState({
                        loading:false,
                        dataSource,
                        total:data.size
                    })
                }else{
                    this.setState({
                        dataSource:[],
                        loading:false
                    })
                }
            }).catch(err=>{
                message.error('搜索失败!');
                console.log(err)
                this.setState({
                    loading:false,
                })
            })
        }else{
            params.tag=searchWord;
            searchCustomComponentByTag(params).then(res=>{
                if(res.data.meta.code==='001'){
                    const data=res.data.data;
                    const dataSource=data.ComponentList.list.map((item,index)=>({
                        ...item,
                        index:index+1+(page-1)*10
                    }))
                    this.setState({
                        loading:false,
                        dataSource,
                        total:data.size
                    })
                }else{
                    this.setState({
                        dataSource:[],
                        loading:false
                    })
                }
            }).catch(err=>{
                message.error('搜索失败!');
                this.setState({
                    loading:false,
                })
            })
        }
    }
    componentDidMount(){
        this.getComponent();
    }
    
    render(){
        const {dataSource,dataInTrash,loading,total,trashTotal,selectedRowKeys,trashPage,page,type,searchWord,visible,radioValue,listLoading,newVisible,current,values,paramsValues,searchType,inputOutput}=this.state;
        const columns=[
            { align: 'center', render: ()=><Avatar size={40} icon={<Icon type="deployment-unit" />} style={{ backgroundColor: '#27afff' }} />},
            // {
            //     title:'序号',
            //     key:'index',
            //     dataIndex:'index',
            //     align:'center'
            // },
            {
                title:'组件名',
                key:'name',
                dataIndex:'name',
                align:'center'
            },
            {
                title:'组件标签',
                key:'tags',
                dataIndex:'tags',
                align:'center',
                render:(text,record)=>{
                    return text.split(',').map(i=>(
                        <Tag color="orange" style={{height:20,padding:'0 5px',marginRight:5,fontSize:12}}>{i}</Tag>
                    ))
                }
            },
            {
                title:'组件描述',
                key:'componentDesc',
                dataIndex:'componentDesc',
                align:'center'
            },
            {
                title:'创建时间',
                key:'createTime',
                dataIndex:'createTime',
                align:'center'
            },
            {
                title:'操作',
                key:'action',
                dataIndex:'action',
                render:(text,record)=>{
                    return <a style={{textDecoration:'none'}} onClick={()=>this.deleteComponent(record.id)}>删除</a>
                },
                align:'center'
            }
        ];
        const columns1=[
            {
                title:'组件名',
                key:'name',
                dataIndex:'name',
                align:'center'
            },
            {
                title:'组件标签',
                key:'tags',
                dataIndex:'tags',
                align:'center',
                render:(text,record)=>{
                    return text.split(',').map(i=>(
                        <Tag color="orange" style={{height:20,padding:'0 5px',marginRight:5,fontSize:12}}>{i}</Tag>
                    ))
                }
            },
            {
                title:'组件描述',
                key:'componentDesc',
                dataIndex:'componentDesc',
                align:'center'
            },
            {
                title:'操作',
                key:'action',
                dataIndex:'action',
                render:(text,record)=>{
                    return (
                        <div>
                            <a onClick={()=>this.deleteComponentPermanently(record.id)}>永久删除</a>
                            <Divider type='vertical' />
                            <a onClick={()=>this.restoreComponent(record.id)}>还原</a>
                        </div>
                    )
                },
                align:'center'
            }
        ]
        const steps = [
            {
              title: '配置组件基本信息',
              content:<ComponentForm onRef={i=>this.componentForm=i} values={values}/>,
            },
            {
              title: '配置组件参数信息',
              content: <ParamsForm onRef={i=>this.paramsForm=i} values={paramsValues}/>,
            },
            {
                title:'配置输入桩和输出桩',
                content:<InputOutputForm onRef={i=>this.inputOutputForm=i} values={inputOutput}/>
            }
          ];
        return (
            <div className='contentWrap'>
                <PageHeader
                    style={{border:'1px solid rgb(235, 237, 240)'}}
                    title='组件管理'
                    subTitle='已有自定义组件列表'
                    extra={[
                        <Button type='primary' onClick={this.openNew}>创建组件</Button>
                    ]}
                />
                <div className='buttonWrap'>
                    <Button type={type==='1'?'primary':''} onClick={()=>this.clickButton('1')}>自定义流程组件</Button>
                    <Button type={type==='0'?'primary':''} onClick={()=>this.clickButton('0')}>自定义算法组件</Button>
                    <Button type={type==='2'?'primary':''} onClick={()=>this.clickButton('2')}>自定义模型组件</Button>
                    <Button type='primary' style={{float:'right'}} onClick={this.openModal}> 回收站</Button>
                    <Input.Search 
                        placeholder='搜索自定义组件' 
                        onSearch={this.handleSearch} 
                        onChange={this.changeSearchWord} 
                        value={searchWord} 
                        style={{width:360, marginRight:'10px'}}
                        addonAfter={
                            <Select value={searchType} onChange={this.changeSearchType} style={{float:'right', width:120}}>
                                <Select.Option value='name'>按名称搜索</Select.Option>
                                <Select.Option value='tag'>按标签搜索</Select.Option>
                            </Select>
                        }
                    /> 
                </div>
                <div>
                    <Spin spinning={loading}>
                        <Table
                            columns={columns}
                            dataSource={dataSource}
                            pagination={{
                                pageSize:5,
                                total,
                                current:page,
                                onChange:this.changePage
                            }}
                        />
                    </Spin>
                </div>
                <Modal
                    title='回收站'
                    visible={visible}
                    footer={null}
                    onCancel={this.closeModal}
                    width={800}
                >
                    <div>
                        <Radio.Group value={radioValue} onChange={this.changeRadioValue}>
                            <Radio value='1'>自定义流程组件</Radio>
                            <Radio value='0'>自定义算法组件</Radio>
                            <Radio value='2'>自定义模型组件</Radio>
                        </Radio.Group>
                        <Spin spinning={listLoading}>
                            <Table
                                columns={columns1}
                                dataSource={dataInTrash}
                                rowSelection={{
                                    onChange:this.changeDeleteItem,
                                    selectedRowKeys
                                }}
                                pagination={{
                                    pageSize: 6,
                                    current:trashPage,
                                    total:trashTotal,
                                    onChange:(page)=>{
                                        this.setState({
                                            trashPage:page
                                        },this.getComponentInTrash)
                                    }
                                  }}
                            />
                        </Spin>
                        <Button type='primary' style={{marginRight:10}} onClick={this.deleteComponentPermanently}>批量删除</Button>
                        <Button type='primary' onClick={this.restoreComponent}>批量还原</Button>
                    </div>
                </Modal>
                <Modal
                    title='创建组件'
                    width={700}
                    visible={newVisible}
                    onCancel={this.closeNew}
                    okText='确定'
                    cancelText='取消'
                    footer={current==0?<Button type='primary' onClick={this.nextStep}>下一步</Button>:
                    current===1?
                    <div>
                        <Button type='primary' onClick={this.preStep}>上一步</Button>
                        <Button type='primary' onClick={this.nextStep}>下一步</Button>
                    </div>:
                    <div>
                        <Button type='primary' onClick={this.preStep}>上一步</Button>
                        <Button onClick={this.closeNew}>取消</Button>
                        <Button type='primary' onClick={this.createComponent}>确定</Button>
                    </div>}
                >
                    <Steps current={current}>
                        {steps.map(item => (
                            <Steps.Step key={item.title} title={item.title} />
                        ))}
                    </Steps>
                    <div>{steps[current].content}</div>
                </Modal>
            </div>
        )
    }
}
export default CustomComponent;
