import React,{Component} from 'react';
import {Button,Input,Checkbox,Select,Form,Upload} from 'antd';
import {getBaseDataSource} from '../../api/process';
import {getModelList} from '../../api/model';

class ComponentForm extends Component{

    state={
        fileList:[],
        list:[
            {id:2,name:123}
        ],
        values:{},
        dataSource:[]
    }
    changeSelect=(v)=>{
        this.props.form.resetFields();
        this.clearFileList();
        if(v=='1'){
            //获取流程列表
            const params = {
                pageNum: 1,
                pageSize: 1000,
                isDeleted:'0',
                // userId:2
              }
            getBaseDataSource(params).then(res=>{
                if(res){
                    if(res.data.meta.code == '001'){
                      const list = res.data.data.WorkflowVOList.map((item,index)=>{
                        return {id:item.id,name:item.name }
                      })
                      this.setState({ 
                        list, 
                      })
                    }else{
                    }
                  }
            })
        }else if(v=='2'){
            //获取模型列表
            const params = {
                pageNum: 1,
                pageSize: 1000,
                // userId:2
              }
              getModelList(params).then(res=>{
                console.log(res)
                if(res){
                  this.setState({loading:false})
                  if(res.data.meta.code == '001'){
                    const list = res.data.data['Model List'].map((item,index)=>{
                      return {id:item.id,name:item.name }
                    })
                    this.setState({
                      list
                    })
                  }else{
                  }
                }
              }).catch(err=>{
                console.log(err)
              })
        }
    }
    clearFileList=()=>{
        this.setState({
            fileList:[]
        })
    }
    validator=(rule, value, callback) => {
        try {
            if(!value){
                throw new Error('请选择上传的算法包');
            }
        } catch (err) {
          callback(err);
        }
      }
    componentDidMount(){
        this.props.onRef(this);
        this.setState({
            values:this.props.values,
            fileList:this.props.values.package?this.props.values.package.fileList:[]
        })
        const {type}=this.props;
        if(type==='process'){

        }else if(type==='model'){

        }
    }
    render(){
        const {getFieldDecorator,getFieldValue}=this.props.form;
        const {fileList,list,values}=this.state;
        const config={
            beforeUpload:(file)=>{
                const {fileList}=this.state;
                fileList.push(file);
                this.setState({
                    fileList
                })
                return false;
            },
            fileList,
            onRemove:(file)=>{
                var {fileList}=this.state;
                const i=fileList.indexOf(file);
                fileList.splice(i,1);
                this.setState({
                    fileList
                })
            }
        }
        const col={
            labelCol:{
                span:5
            },
            wrapperCol:{
                span:10
            },

        }
        const rules=[{required:true,whitespace: true,}]
        return (
            <div>
                <Form colon={false} >
                    <Form.Item label='选择组件类型' {...col}>
                        {
                            getFieldDecorator('type',{
                                rules,
                                initialValue:values['type']
                            })(<Select onChange={this.changeSelect}>
                                <Select.Option value="1">流程组件</Select.Option>
                                <Select.Option value="0">算法组件</Select.Option>
                                <Select.Option value="2">模型组件</Select.Option>
                            </Select>)
                        }
                    </Form.Item>
                    {
                    getFieldValue('type')?<div>
                        <Form.Item label='组件名称' {...col}>
                            {
                                getFieldDecorator('name',{
                                    rules,
                                    initialValue:values['name']?values['name']:''
                                })(<Input placeholder='请输入组件名称'/>)
                            }
                        </Form.Item>
                        <Form.Item label='组件标签' {...col}>
                            {
                                getFieldDecorator('tags',{
                                    rules,
                                    initialValue:values['tags']?values['tags']:''
                                })(<Input placeholder='请输入组件标签以‘，’隔开'/>)
                            }
                        </Form.Item>
                        <Form.Item label='组件描述' {...col}>
                            {
                                getFieldDecorator('componentDesc',{
                                    rules,
                                    initialValue:values['componentDesc']?values['componentDesc']:''
                                })(<Input placeholder='请输入组件描述'/>)
                            }
                        </Form.Item>
                        <Form.Item label='图类型' {...col}>
                            {
                                getFieldDecorator('graphTypeIds',{
                                    rules,
                                    initialValue:values['graphTypeIds']?values['graphTypeIds']:''
                                })(<Input placeholder='请输入图类型'/>)
                            }
                        </Form.Item>
                        {
                            getFieldValue('type')==='0'?
                            <Form.Item label='算法包' {...col}>
                                {
                                    getFieldDecorator('package',{
                                        rules:[{required:true}]
                                    })(<Upload {...config}><Button icon='upload'>上传</Button></Upload>)
                                }
                            </Form.Item>:null
                        }
                        {
                            getFieldValue('type')==='1'?
                            <Form.Item label='流程' {...col}>
                                {
                                    getFieldDecorator('sourceId',{
                                        rules,
                                        initialValue:values['sourceId']?values['sourceId']:null
                                    })(<Select dropdownStyle={{height:150,overflow:'auto'}}>
                                        {
                                            list&&list.map(item=>(
                                                <Select.Option value={item.id+''}>{item.name}</Select.Option>
                                            ))
                                        }
                                    </Select>)
                                }
                            </Form.Item>:''
                        }
                        {
                            getFieldValue('type')==='2'?
                            <Form.Item label='模型' {...col}>
                                {
                                    getFieldDecorator('sourceId',{
                                        rules,
                                        initialValue:values['sourceId']?values['sourceId']:null
                                    })(<Select dropdownStyle={{height:150,overflow:'auto'}}>
                                        {
                                            list&&list.map(item=>(
                                                <Select.Option value={item.id+''}>{item.name}</Select.Option>
                                            ))
                                        }
                                    </Select>)
                                }
                            </Form.Item>:''
                        }
                        {/* <Form.Item label='输入桩参数名称' {...col}>
                            {
                                getFieldDecorator('inputName',{
                                    rules,
                                    initialValue:values['inputName']?values['inputName']:''
                                })(<Input placeholder='请输入输入桩参数名称'/>)
                            }
                        </Form.Item> */}
                        {/* <Form.Item label='输入桩参数类型' {...col}>
                            {
                                getFieldDecorator('inputType',{
                                    rules,
                                    initialValue:values['inputType']?values['inputType']:''
                                })(<Input placeholder='请输入输入桩参数类型'/>)
                            }
                        </Form.Item>
                        <Form.Item label='输出桩参数名称' {...col}>
                            {
                                getFieldDecorator('outputName',{
                                    rules,
                                    initialValue:values['outputName']?values['outputName']:''
                                })(<Input placeholder='请输入输出桩参数名称'/>)
                            }
                        </Form.Item>
                        <Form.Item label='输入桩参数类型' {...col}>
                            {
                                getFieldDecorator('outputType',{
                                    rules,
                                    initialValue:values['outputType']?values['outputType']:''
                                })(<Input placeholder='请输入输入桩参数类型'/>)
                            }
                        </Form.Item> */}
                    </div>:null
                    }
            </Form>
        </div>
        )  
    }
}
export default Form.create()(ComponentForm);