import React, { Component } from 'react'
import { Input, Form, Upload, Button, Icon, message } from 'antd'

import { insertUserDataset, uploadDataset} from '../../api/dataset';


class UploadDataset extends Component {

  constructor(props){
    super(props)
    this.state = {
      fileList: [],
      k:0
    }
  }

  componentDidMount() {
    this.props.onRef(this)
  }

  submit = () => {
    const { getFieldValue, validateFields } = this.props.form
    validateFields((err,values) => {
      if(!err){
        let params = {
          datasetName: values.sourceName,
          tags: values.tags.split(','),
          datasetDesc: values.desc,
          // columnSeparator:values.colSpilt,
          // familyName:JSON.stringify(values.family),
          file:this.state.fileList[0],
          // userId:2
          }
          // const columnName=[];
          // for(let i=0; i<values.family.length; i++){
          //   const arr=values.columnName[i].split(',');
          //   for(let j=0; j<arr.length; j++){
          //     var str =values.family[i]+':'+arr[j];
          //     columnName.push(str);
          //   }
          // }
          // params.columnName=JSON.stringify(columnName);
          console.log(params)
          //异步请求
          uploadDataset(params).then(res => {
          // insertUserDataset(params).then((res)=>{
            const newRes=res.data.meta;
            if(newRes.code==='001'){
              message.success(newRes.message)
              this.props.getUploadData();
            }else{
              message.error(newRes.message)
            }
          }).catch(err=>{
            console.log(err)
          })
          this.props.close();
      }
    });
  }

  clear = () => {
    this.setState({
      fileList: []
    })
    this.props.form.resetFields()
  }
  remove=k=>{
    const { form } = this.props;
    const keys = form.getFieldValue('keys');
    const newKeys=keys.filter(key => key !== k);
    form.setFieldsValue({
      keys:newKeys,
    });
  };
  add=()=>{
    const { form } = this.props;
    const {k}=this.state;
    const keys = form.getFieldValue('keys');
    const nextKeys = keys.concat(k+1);
    form.setFieldsValue({
        keys:nextKeys
    });
    this.setState({
        k:k+1
    })
  };

  render(){
    const { fileList} = this.state;
    const { getFieldDecorator,getFieldValue } = this.props.form;
    const props = {
      onRemove: file => {
        this.setState(state => {
          const index = state.fileList.indexOf(file);
          const newFileList = state.fileList.slice();
          newFileList.splice(index, 1);
          return {
            fileList: newFileList,
          };
        });
      },
      beforeUpload: file => {
        this.setState(state => ({
          fileList: [...state.fileList, file],
        }));
        return false;
      },
      onChange:  info => {
        let fileList = [...info.fileList];
        console.log(fileList);
        fileList = fileList.slice(-1);
        console.log(fileList);
      },
      fileList,
    };
    const rules=[
      {
          required: true,
          whitespace: true,
      },
  ]
    getFieldDecorator('keys', { initialValue:[0]});
    const keys = getFieldValue('keys');
    // const formItems = keys.map((k, index) => (
    //     <div style={{position:'relative'}}>
    //       <Form.Item
    //         label={'列族'}
    //         key={k+'1'}
    //       >
    //         {getFieldDecorator(`family[${k}]`, {
    //             rules,
    //         })(<Input placeholder="请输入列族名称" />)}
    //       </Form.Item>
    //       <Form.Item
    //         label={'列名'}
    //         key={k+'2'}
    //       >
    //         {getFieldDecorator(`columnName[${k}]`, {
    //             rules,
    //         })(<Input placeholder="请输入列名，如有多个列名使用逗号(,)隔开" />)}
    //       </Form.Item>
    //       <Icon
    //         type="minus-circle-o"
    //         onClick={() => this.remove(k)}
    //         style={{position:'absolute',right:-18,bottom:13}}
    //       />
    //     </div>
        
    // ));
    return(
      <>
        <Form labelAlign='left'> 
          <div>
            <span style={{color:'red'}}></span>支持csv、txt格式
          </div>
          <Form.Item label='数据集名称'>
            {getFieldDecorator('sourceName',{rules: [{ required: true, message: 'Please input!' }]})
              (<Input placeholder="请输入数据集名称"/>)} 
          </Form.Item>

          {/* <Form.Item label='行分割符'>
            {getFieldDecorator('rowSpilt',{initialValue:''})
              (<Input/>)} 
          </Form.Item> */}

          {/* <Form.Item label='列分割符'>
            {getFieldDecorator('colSpilt',{initialValue:','})
              (<Input/>)} 
          </Form.Item> */}

          <Form.Item label='数据集标签'
            name='tags'
          >
            {getFieldDecorator('tags', {initialValue:'',rules: [{ required: true, message: 'Please input!' }]})
            (<Input.TextArea 
              placeholder='请输入标签，并以 , 分割'/>
              )}
          </Form.Item>

          <Form.Item label='数据集描述'>
            {getFieldDecorator('desc',{initialValue:''})(<Input.TextArea placeholder="请输入数据集描述" />)} 
          </Form.Item>

          <Form.Item required={true}>
            {getFieldDecorator('file',{rules: [{ required: true, message: 'Please input!' }]})(
            <Upload {...props}>
              <Button>
                <Icon type="upload" /> 选择文件
              </Button>
            </Upload>
            )}
            
          </Form.Item>

          {/* {
            formItems
          } */}
          {/* <Form.Item>
            <Button type="dashed" onClick={this.add} style={{width:300,marginLeft:80}}>
                <Icon type="plus" /> 添加列族
            </Button>
          </Form.Item> */}

          <Form.Item style={{textAlign:'center'}}>
            <Button style={{marginRight:'5px'}} type='primary'
              onClick={this.clear}
            >
              重置
            </Button>
            <Button style={{marginLeft:'5px'}} type='primary'
              onClick={this.submit}
            >
              注册
            </Button>
          </Form.Item>
        </Form>
      </>
    )
  }
}

export default Form.create()(UploadDataset);