import React from 'react';
import { Row, Col, Input, PageHeader, Button } from 'antd';
import GGEditor, { Flow } from 'gg-editor';
import { withRouter } from 'react-router-dom';
import {connect} from 'react-redux'

import { FlowContextMenu } from './components/EditorContextMenu';
import { FlowToolbar } from './components/EditorToolbar';
import { FlowItemPanel } from './components/EditorItemPanel';
import CustomNode from './components/node/nodeTensorflow';
import StartNode from './components/node/nodeStart';
import styles from './index.module.less'
import {actionCreator} from './store'

@withRouter
class CreateProcess extends React.Component {

  constructor(props){
    super(props);
    
    this.state = {
    }
  }


  componentWillMount(){
    const {location} = this.props
    if(location.query &&  (location.query.canEdit===false || location.query.canEdit)){  // 查看流程的情况  canEdit表示该流程是否可编辑（有实验的流程不能编辑）
      const {canEdit} = location.query
      this.setState({canEdit})
      const processObj = this.props.processData ? JSON.parse(this.props.processData) : {}
      if(processObj){
        processObj.nodes && processObj.nodes.map(item=>{
          item.id = item.id.toString()  // 将id转为字符串
        });
        processObj.edges && processObj.edges.map((item,index)=>{
          item.id = (item.id + index).toString();
          item.source = item.source.toString();
          item.target = item.target.toString();
        })
      }
      this.setState({processObj})
    }
  }

  componentWillUnmount(){
    this.props.saveProcess('')
  }

  render() {
    const {canEdit} = this.state
    return (<div className={styles.canvasContainer}>
        <GGEditor 
          className={styles.editor}
          onBeforeCommandexecute={({ command }) => {
            console.log('command', command);
          }}
        >
          <PageHeader
            className={styles.pageheader}
            title="流程管理"
            onBack={() => this.props.history.push('/index/process')}
            subTitle="请拖拽节点创建一个流程"
            // extra={[canEdit && <Button type="primary">保存流程</Button>]}
          />
          <Row type="flex" className={styles.editorBd} style={{height:'100%'}}>
            {(canEdit || canEdit === undefined) && <Col xxl={4} xl={5} lg={6} md={7} sm={8} xs={8} className={styles.editorSidebar}>
            <Input.Search
              placeholder="搜索组件"
              style={{margin: '5px 0', width: '95%', textAlign: 'center', alignSelf: 'center'}}
              enterButton
              size="large"
              onSearch={value => console.log(value)}
            />
              <FlowItemPanel />
            </Col>}
            <Col xxl={20} xl={19} lg={18} md={17} sm={16} xs={16} className={styles.editorContent}>
            {(canEdit || canEdit === undefined) && <FlowToolbar />}
              <CustomNode />
              <StartNode />
              <Flow 
                className={styles.flow}
                data={this.state.processObj}
              />
            </Col>
          </Row>
          {(canEdit || canEdit === undefined) && <FlowContextMenu />}
        </GGEditor>
   </div> );
  }
};

const mapStateToProps = (state) => {
  return {
    processData: state.process.processData,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    saveProcess(process){
      dispatch(actionCreator.saveProcess(process))
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(CreateProcess);
