import Axios from '../utils/request';

// 获取所有的系统组件
export async function loadPublicComponentInfo(params){
  return Axios({
    url: '/publicComponent/loadPublicComponentInfo',
    method: 'POST',
    params
  })
}

export async function getCustomComponent(params) {
    return Axios({
      url: '/customComponent/loadCustomComponentInfo',
      method: 'POST',
      params
    })
}
export async function deleteCustomComponent(params) {
    return Axios({
      url: '/customComponent/deleteComponent',
      method: 'POST',
      params
    })
}
export async function deleteCustomComponentPermanently(params) {
    return Axios({
      url: '/customComponent/deleteComponentPermanently',
      method: 'POST',
      params
    })
}
export async function restoreCustomComponent(params) {
    return Axios({
      url: '/customComponent/restoreComponent',
      method: 'POST',
      params
    })
}
export async function searchCustomComponentByName(params) {
    return Axios({
      url: '/customComponent/getUserCustomComponentByKeyword',
      method: 'POST',
      params
    })
}
export async function searchCustomComponentByTag(params) {
    return Axios({
      url: '/customComponent/getUserCustomComponentByTag',
      method: 'POST',
      params
    })
}
export async function getCustomComponentInTrash(params) {
    return Axios({
      url: '/customComponent/getComponentInTrash',
      method: 'POST',
      params
    })
}
export async function createCustomComponent(params) {
  const formData=new FormData();
  for(let key in params){
    formData.append(key, params[key]);
  }
    return Axios({
      url: '/customComponent/createComponent',
      method: 'POST',
      data:formData,
    })
}