import React from 'react'
import {  Dropdown, Menu, message, Avatar, Icon} from 'antd'
import { Link, withRouter } from 'react-router-dom'
import { isAuthenticated } from '../../utils/Session'
// import { MenuUnfoldOutlined, MenuFoldOutlined,} from '@ant-design/icons';
import {logout} from '../../api/login'
import Style from './index.module.less'

@withRouter 
class HeaderBar extends React.Component {
  state = {
    username: '',
    avatar: require('./img/04.jpg')
  }

  toggle = () => {
    this.props.onToggle()
  }

  componentDidMount(){
    this.setState({username: localStorage.getItem('username')})
  }

  logout = () => {
    // this.props.appStore.toggleLogin(false)
    // this.props.history.push(this.props.location.pathname)
    logout().then(res=>{
      if(res){
        if(res.data.meta.code === "001"){
          this.props.history.push('/login')
          message.success(res.data.meta.message)
        }else{
          message.error(res.data.meta.message)
        }
      }
    })
  }

  render () {
    const { count, avatar} = this.state
    const {appStore, collapsed, location} = this.props
    const notLogin = (
      <div>
        <Link to={{pathname: '/login', state: {from: location}}} style={{color: 'rgba(0, 0, 0, 0.65)'}}>登录</Link>&nbsp;
        <img src={require('./img/04.jpg')} alt=""/>
      </div>
    )
    const menu = (
      <Menu className='menu'>
        {/* <Menu.ItemGroup title='用户中心' className='menu-group'> */}
        {/* <Menu.Item>你好 - {isAuthenticated()}</Menu.Item>
        <Menu.Item>个人信息</Menu.Item> */}
        <Menu.Item><span onClick={this.logout}>退出登录</span></Menu.Item>
        {/* </Menu.ItemGroup> */}
        {/* <Menu.ItemGroup title='设置中心' className='menu-group'>
          <Menu.Item>个人设置</Menu.Item>
          <Menu.Item>系统设置</Menu.Item>
        </Menu.ItemGroup> */}
      </Menu>
    )
    return (
      <div className={Style.HeaderBar}>
        <Icon
          className={Style.trigger}
          type={this.state.collapsed ? 'menu-unfold' : 'menu-fold'}
          onClick={this.toggle}
        />
        <Dropdown overlay={menu} placement="bottomCenter" className={Style.avatar}>
          <Avatar src={avatar} size='large' />
        </Dropdown>
      </div>
    )
  }
}

export default HeaderBar