import React from 'react'
import {Layout} from 'antd'
import SiderNav from './SiderNav'
import ContentMain from './ContentMain'
import HeaderBar from './HeaderBar'


const {Sider, Header, Content, Footer} = Layout


class Index extends React.Component{
  state = {
    collapsed: false
  }

  toggle = () => {
    // console.log(this)  状态提升后，到底是谁调用的它
    this.setState({
      collapsed: !this.state.collapsed
    })
  }
  render() {
    const {collapsed} = this.state
    return (
        <Layout>
          <Sider collapsible
                 trigger={null}
                 collapsed={collapsed}
                 style={{
                    overflow: 'auto',
                    height: '100vh',
                    position: 'fixed',
                    left: 0,
                  }}
                 >
           <SiderNav/>
          </Sider>
          <Layout style={{ marginLeft: collapsed ? 80 : 200, minHeight: '100vh' }}>
            <Header style={{background: '#fff', padding: 0, height: '50px', margin:0, position: 'fixed', zIndex: 1, width: `calc(100% - ${collapsed ? 80 : 200}px)`}}>
              <HeaderBar collapsed={this.state.collapsed} onToggle={this.toggle}/>
            </Header>
            <Content style={{marginTop: 50}}>
              <ContentMain/>
            </Content>
            <Footer style={{textAlign: 'center', padding: 0}}>AIFlow ©2020 Created by bdilab</Footer>
          </Layout>
        </Layout>
    );
  }
}
export default Index