import React, { Component } from 'react';
import { PageHeader, Table, message, Spin, Divider, Button, Modal, Row, Col, Input, Form,
     Card, Empty, Tag, Select, Avatar, Icon} from 'antd';

import {getUploadDataset,getPublicDataset,deleteDataset,
  searchPublicDatasetByName,searchUserDatasetByName,searchPublicDatasetByTags,
    searchUserDatasetByTags,downloadDataset,previewDataset} from '../../api/dataset';
import UploadDataset from './UploadDataset';
import Trash from './trash'
import style from './index.module.less'

class  Dataset extends Component{

  constructor(props){
    super(props)
    this.state = {
      //测试数据
      dataSource: [],
      loading: false,
      visible: false,
      loadingButton: false,
      radioValue: 'user',
      previewVisible: false,
      previewColumns: [],
      previewData: [],
      previewPage:1,
      trashVisible:false,
      total:1,
      searchType:'name',
      page:1
    }
  }

  componentDidMount(){
    this.getUploadData();
  }


  //异步获取公开数据集
  getData = () => {
    this.setState({loading: true})
    const {page} = this.state
    const params = {
      pageNum:page,
      pageSize:7
    }
    getPublicDataset(params).then(res=>{
      console.log(res)
      if(res){
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data['Dataset List'].map((item,index)=>{
            return { ...item, index: index+1+(page-1)*6 }
          })
          this.setState({ 
            dataSource, 
            loading: false,
            total: res.data.data.Total
          })
        }else{
          message.error('获取数据出错！')
          this.setState({ loading: false })
        }
      }
    }).catch(err=>this.setState({ loading: false }))
  }

  //异步获取自定义数据集
  getUploadData = () => {
    this.setState({loading: true, radioValue: 'user'})
    const {page} = this.state
    const params = {
      pageNum: page,
      pageSize: 7,
    }
    getUploadDataset(params).then(res=>{
      console.log(res)
      if(res){
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data['Dataset List'].map((item,index)=>{
            return { ...item, index: index+1+(page-1)*6 }
          })
          this.setState({ 
            dataSource, 
            loading: false,
            total: res.data.data['Total']
          })
        }else{
          message.error('获取数据出错！')
          this.setState({ loading: false })
        }
      }
    }).catch(err=>this.setState({ loading: false }))
  }

  //处理删除自定义数据集
  handleDelect = (record) => {
    console.log(record)
    const params = {datasetId:record.id}
    deleteDataset(params).then(res=>{
      if(res){
        console.log(res)
        if(res.data.meta.code == '001'){
          message.success('删除成功！')
          this.getUploadData()
        }else{
          message.error('删除失败！')
        }
      }
    })
  }
  //切换数据集类型
  radioChange = val => {
    this.setState({
      radioValue: val,
      search:'',
      page: 1
    },()=>{
      console.log(this.state.radioValue)
      const { radioValue } = this.state
      radioValue === 'user' && this.getUploadData()
      radioValue === 'classic' && this.getData()
    });
  };
  //关闭注册数据集的modal框
  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };
  //展示注册数据集modal框
  showModal = () => {
    this.setState({
      visible: true,
    });
  };
  

  //数据集预览
  preview = record => {
    // console.log(record)
    this.setState({
      previewVisible: true,
      previewLoading: true
    })
    let previewColumns,previewData,previewDesc
    previewDataset({datasetId:record.id}).then(res=>{
      console.log(res)
      if(res){
        this.setState({previewLoading: false})
        if(res.data.meta.code === '001'){
          previewDesc = res.data.data.desc
          const {content} = res.data.data
          previewColumns = content[0] && content[0].map(item =>{
            return {
              title: item,
              dataIndex: item,
              key: item
            }
          })
          previewData = content.map(item=>{
            let preview = {}
            for(let i=0;i<item.length;i++){
              preview = { ...preview, [previewColumns[i].title]:item[i] }
            }
            return preview
          })
          this.setState({
            previewColumns,
            previewData,
            previewDesc
          })
        }
      }
    })
  }
  //预览数据集关闭
  handlePreCancel = () => {
    this.setState({
      previewVisible: false,
      previewPage: 1,
      previewData:[],
      previewDesc:''
    })
  }
  //数据集换页
  datasetPageChange = (page) => {
    console.log(page);
    this.setState({page},()=>{
      if(this.state.search){
        this.state.radioValue === 'user' && this.userSearch()
        this.state.radioValue === 'classic' && this.classicSearch()
      }else{
        this.state.radioValue === 'user' && this.getUploadData()
        this.state.radioValue === 'classic' && this.getData()
      }
    });
  }
  //换页 
  pageChange = (page) => {
    this.setState({
      previewPage: page
    })
  }


  //搜索
  handleSearch = search => {
    const { radioValue } = this.state
    this.setState({
      loading:true,
      page:1,
      search
    }, function(){
      if(radioValue === 'classic'){
        this.classicSearch(search)
      }else if(radioValue === 'user'){
        this.userSearch(search)
      }
    })
  }

  classicSearch = search => {
    const {searchType}=this.state;
    const {page} = this.state
    const params = {
      pageNum: page,
      pageSize: 7,
    }
    if(searchType=='name'){
      params.datasetName=search
      searchPublicDatasetByName(params).then(res=>{
        console.log(res)
        if(res){
          if(res.data.meta.code == '001'){
            const dataSource = res.data.data['Dataset List'].map((item,index)=>{
              return { ...item, index: index+1+(page-1)*10 }
            })
            this.setState({ 
              dataSource, 
              loading: false,
              total: res.data.data.Total
            })
          }else{
            message.error('获取数据出错！')
            this.setState({ loading: false })
          }
        }
      }).catch(err=>this.setState({ loading: false }))
    }else{
      params.datasetTags=search;
      searchPublicDatasetByTags(params).then(res=>{
        console.log(res)
        if(res){
          if(res.data.meta.code == '001'){
            const dataSource = res.data.data['Dataset List'].map((item,index)=>{
              return { ...item, index: index+1+(page-1)*10 }
            })
            this.setState({ 
              dataSource, 
              loading: false,
              total: res.data.data.Total
            })
          }else{
            message.error('获取数据出错！')
            this.setState({ loading: false })
          }
        }
      }).catch(err=>this.setState({ loading: false }))
    }
  }

  userSearch = search => {
    const {searchType}=this.state;
    const {page} = this.state
    const params = {
      pageNum: page,
      pageSize: 7,
      // userId:2
    }
    if(searchType=='name'){
      params.datasetName=search;
      searchUserDatasetByName(params).then(res=>{
        console.log(res)
        if(res){
          if(res.data.meta.code == '001'){
            const dataSource = res.data.data['Dataset List'].map((item,index)=>{
              return { ...item, index: index+1+(page-1)*10 }
            })
            this.setState({ 
              dataSource, 
              loading: false,
              total: res.data.data.Total
            })
          }else{
            message.error('获取数据出错！')
            this.setState({ loading: false })
          }
        }
      }).catch(err=>this.setState({ loading: false }))
    }else{
      params.datasetTags=search;
      searchUserDatasetByTags(params).then(res=>{
        console.log(res)
        if(res){
          if(res.data.meta.code == '001'){
            const dataSource = res.data.data['Dataset List'].map((item,index)=>{
              return { ...item, index: index+1+(page-1)*10 }
            })
            this.setState({ 
              dataSource, 
              loading: false,
              total: res.data.data.Total
            })
          }else{
            message.error('获取数据出错！')
            this.setState({ loading: false })
          }
        }
      }).catch(err=>this.setState({ loading: false }))
    }
  }

  onSearchChange = e =>{
    this.setState({
      search: e.target.value
    })
  }
  changeSearchType=(v)=>{
    this.setState({
      searchType:v
    })
  }
  downloadDataset=(record)=>{
    const params={datasetId:record.id};
    downloadDataset(params).then(res=>{
      console.log(res)
      if(res){
        var blob = new Blob([res.data.data])
        console.log(blob)
        var href = window.URL.createObjectURL(blob);
        var link = document.createElement('a');
        link.setAttribute("download", 'result.csv');
        link.href = href;
        link.click();
        message.success('导出成功!')
      }
    }).catch(err=>{})
  }

  render(){
    const { searchType,dataSource,previewColumns, previewData, previewDesc, total, radioValue, trashVisible} = this.state

    //数据集表格结构
    const columns = [
      { align: 'center', render: ()=><Avatar size={40} icon={<Icon type="file-text" theme="filled" />} style={{ backgroundColor: '#27afff' }} />},
      // {title: '序号', dataIndex: 'index', key: 'index',},
      {title: '数据集名称', dataIndex: 'name', key: 'name',},
      {title: '标签',dataIndex: 'tags', key: 'tags',
        render:tags=>(
          <>
            {tags.split(',').map((item,index)=>(
              <Tag color='orange' key={index}>{item}</Tag>
            ))}
          </>
        )},
      { title: '创建时间',dataIndex: 'createTime', key: 'createTime',},
      {title: '数据集类型', dataIndex: 'type',key: 'type',
        render: (text, record) => <span>{ text===0 ? '公开数据集' : text ===1 ? '自定义数据集' : '' }</span>
      },
      {title: '数据集描述',dataIndex: 'datasetDesc', key: 'datasetDesc',},
      {title: '操作',key: 'action',
        render: (text, record) => (
          <span>
            <a onClick={()=>this.preview(record)}>预览</a>
            <Divider type="vertical" />
            <a onClick={()=>this.downloadDataset(record)}>导出</a>
            {this.state.radioValue === 'user' &&  <Divider type="vertical" />}
            {this.state.radioValue === 'user' &&  <a onClick={e=>this.handleDelect(record)}>删除</a> }
          </span>
        )},
    ];

  return(
      <div className={style.contentWrap}>
        <PageHeader
          style={{border: '1px solid rgb(235, 237, 240)',marginBottom: '13px'}}
          title="数据集管理" subTitle="已有数据集列表"
          extra={[<Button type='primary' onClick={this.showModal}>注册数据集</Button>]}
        />
        <div className={style.buttonWrap}>
          <Button type={radioValue==='classic'?'primary':''} onClick={()=>this.radioChange('classic')}>公开数据集</Button>
          <Button type={radioValue==='classic'?'':'primary'} onClick={()=>this.radioChange('user')} style={{marginLeft:'10px'}}>自定义数据集</Button>
          <Button type='primary' style={{float:'right'}} onClick={()=>{
                this.setState({trashVisible:true})
                this.trash ? this.trash.getDatasetData() : console.log('第一次加载')
            }}>回收站</Button>
          
          <Input.Search 
              onSearch={value=>this.handleSearch(value)} 
              value={this.state.search}
              onChange={this.onSearchChange}
              placeholder="搜索数据集"
              style={{float:"right",width:360, marginRight:'10px'}}
              addonAfter={
                <Select value={searchType} onChange={this.changeSearchType} style={{float:"right",width:120}}>
                  <Select.Option value='tag'>按标签搜索</Select.Option>
                  <Select.Option value='name'>按名称搜索</Select.Option>
                </Select>
              }
          />
        </div>
        
        <Spin spinning={this.state.loading} tip='loading...'>
          <Table 
            columns={columns} 
            dataSource={dataSource} 
            pagination={{
              onChange:this.datasetPageChange,
              current:this.state.page,
              total,
              pageSize: 7,
            }}
          />
        </Spin>
        
        {/* 下面是各个modal框 */}
        <Modal
          title="注册数据集"
          footer={null}
          visible={this.state.visible}
          onCancel={()=>{
            this.setState({visible: false})
            this.upload.clear();
            this.getUploadData()
        }}
        >
          <UploadDataset  close={()=>{
            this.setState({visible: false});
          }} onRef={ref=>this.upload=ref} getUploadData={this.getUploadData}/>
        </Modal>

        <Modal
          title="回收站"
          visible={trashVisible}
          footer={null}
          onCancel={()=>{
            this.setState({trashVisible:false})
            radioValue=='classic'?this.getData():this.getUploadData();
          }}
        >
          <Trash onRef={ref=>this.trash=ref}/>
        </Modal>
        {/* 这个是copy智能平台，暂时没进行修改 */}
        <Modal
          title="预览数据集"
          visible={this.state.previewVisible}
          onCancel={this.handlePreCancel}
          footer={null}
          width={'85%'}
        >
          <Spin tips='loading' spinning={this.state.previewLoading}>
            <Row>
              <Col span={17}>
                <Table 
                  columns={previewColumns} 
                  dataSource={previewData} 
                  size="small" 
                  showHeader={false}
                  pagination={{current:this.state.previewPage,onChange:this.pageChange}}
                />
              </Col>
              <Col span={6} offset={1}>
                <Card style={{maxHeight:'420px',overflow:'auto'}}>
                  <p>{previewDesc ? previewDesc: <Empty description='暂无描述' /> }</p>
                </Card>
              </Col>
            </Row>
          </Spin>
        </Modal>
      </div>
  )
  }
}

export default Form.create()(Dataset);